    checkMtCore(item) {
      let vm = this;
      let result = "";
      let ref = vm.soureTable + ".mtCore";
      let arrKey = ref.split(".");
      for (var i = 0; i < arrKey.length; i++) {
        let key = arrKey[i];
        if (typeof result === "object" && result.constructor === Object) {
          result = result[key];
        } else {
          result = item[key];
        }
      }
      if (result && (result === 1 || result === "1")) {
        return true;
      } else {
        return false;
      }
