      } else if (moduleVrVehicletypeCertificate == '05') {
        vm.tenModuleTSKT = 'CHỨNG NHẬN MTTNL SXLR';
      } else if (moduleVrVehicletypeCertificate == '06') {
        vm.tenModuleTSKT = 'CHỨNG NHẬN MTTNL NK';
      } else {
        vm.tenModuleTSKT = '';
      }

